@charset "utf-8";

/* Reset */
html, body, div, span, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre,abbr, address, cite, code,del, dfn, em, img, ins, kbd, q, samp,small, strong, sub, sup, var,b, i,dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td,article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,time, mark, audio, video,select,option {margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline;letter-spacing:-0.05em;background:transparent;box-sizing:border-box}
article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {display:block}
body {word-break:keep-all;word-wrap:break-word;-webkit-text-size-adjust:none}
body,th,td,input,select,textarea,button {font-size:14px;line-height:1.3;font-weight:400;font-family:'Noto Sans KR', sans-serif;color:#575757}
blockquote, q {quotes:none}
blockquote:before, blockquote:after,
q:before, q:after {content:'';content:none}
a {margin:0;padding:0;font-size:100%;vertical-align:baseline;text-decoration:none;background:transparent;color:#575757}
a:active, a:hover {text-decoration:underline;cursor:pointer;}
nav,dl,ul,ol,menu,li {list-style:none}
address,caption,cite,code,dfn,em,var {font-style:normal;font-weight:normal}
table {border-collapse:collapse;border-spacing:0}
button,input,textarea {margin:0;padding:0;border:0;outline:none;font-size:100%;vertical-align:baseline;background:transparent;cursor:pointer}
img {vertical-align:middle;max-width:100%}
video {width:100%}
input:checked[type=checkbox]{background-color:#666;-webkit-appearance:checkbox}
input[type=button],input[type=email],input[type=password],input[type=reset],input[type=search],input[type=submit],input[type=tel],input[type=text],input[type=url],textarea,select{-webkit-appearance:none;border-radius:0}
legend,caption {display:block;position:absolute;left:-9999px;width:1px;height:1px;font-size:0;line-height:0;overflow:hidden;text-indent:-9999px}
.blind{position:absolute !important;clip:rect(0 0 0 0) !important;width:1px !important;height:1px !important;margin:-1px !important;overflow:hidden !important}
.clearbx:after,.clearfix:after{content:'';clear:both;display:table}
.ellipsis{overflow:hidden;white-space:nowrap;word-break:break-all;text-overflow:ellipsis}
.valign:after {content:"";display:inline-block;width:0;vertical-align:middle;height:100%;}
.smallfont{font-family:'돋움',Dotum,Helvetica,'Apple SD Gothic Neo',sans-serif !important}


/* Placeholder */
::-webkit-input-placeholder {font-size:12px;color:#a1aec4;} /* WebKit browsers */
:-moz-placeholder {font-size:12px;color:#a1aec4;opacity:1;} /* Mozilla Firefox 4 to 18 */
::-moz-placeholder {font-size:12px;color:#a1aec4;opacity:1;} /* Mozilla Firefox 19+ */
:-ms-input-placeholder {font-size:12px;color:#a1aec4 !important;} /* Internet Explorer 10+ */

/* HEADER */
.header{
  height: 60px;
  width: 1024px;
  padding: 10px 5px;
  text-align: center;
  margin: auto;
}

.header_logo{
  font-family: 'Anton';
  font-size: 20pt;
  position: absolute;
  left: 0;
  right: 0;
}

.menu{
  float: right;
  margin-right: 10px;
}



/* MAIN CONTAINER */
.container{
  width: 100%;
}

.page{
  width: 100%;
  background-color: #b2dfdb;
}

.inner_page{
  width:1024px;
  margin:auto;
}

.inner_page h6{
  color: #fff;
  font-weight: bold;
  padding-top:30px;
}

.inner_page a{
  color: #fff;
}

.paper{
  padding: 10px;
}

.history_date{
  color: #000;
}

.content{ 
  height: 680px;
  position: relative;
}


.left_box{
  width: 400px;
  text-align: right;
  position: relative;
  height:100%;

}

.my_avatar{
  position: absolute;
  left:10px;
  top:55px;
}

.intro_name{
  margin-top:50px;
  line-height: 1.4;
}

.intro_name h2{
  font-size: 20pt;
}

.my_name{
  font-size: 16pt;
}

.left_box .description{
  font-size : 14px;
  text-align: right;
  line-height: 1.5;
}

.right_box{
  position: absolute;
  top: 5px;
  right: 20px;
  width: 580px;
}

.skills{
  text-align: left;
  margin-top:30px
}

.skills h2{
  color:#009688;
  font-size: 18pt;
}

.lineProgress{
  width:200px;
  float: right;
  margin-top:4px;
}

.skills_content{
  padding-left: 30px;
}

.skills_content p{
  margin-bottom:10px;
}

.history a{
  text-decoration: underline;
}

.project_item{
  padding-bottom: 30px;
  position: relative;
}

.image_box{
  text-align: right;
  margin-bottom: 15px
}

.project{
  margin-top:30px
}

.image_icon{
  height: 100px;
  display: inline-block;
  background-size: cover;
  background-position: 50% 50%;
  width: 100px;
  margin-right: 10px;
  box-shadow: 3px 3px 0 #ddd;
  cursor: pointer;
}

.project_item h3{
  font-size: 22px;
  color:#009688;
}

.project_item h3 span{
  font-size: 14px;
  margin-left: 8px;
  color:#575757
}

.project_item .description{
  font-size: 16px;
  padding-left : 10px;
  margin:5px 0px 15px 0px;
}

.icon_list{
  display: flex;
}

svg{
  width: 48px;
  height: 48px;
}
.icon_list svg{
  margin-right: 5px;
}
.project_date{
  position: absolute;
  left: 5px;
  bottom: 50px;
  color: #af4f4f;
}